import React, { useRef, useState, useEffect, useCallback } from "react";
import {
  Container,
  BUTTON_R,
  Button,
  CarouselNav,
  BUTTON_L,
  In,
  ImageContainer,
  List,
  Img,
} from "./galleryelements";
import { Place,Place1 } from "../Motto/mottoelements";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChevronLeft as l,
  faChevronRight as r,
} from "@fortawesome/free-solid-svg-icons";

import "./gallery.css";

const Gallery = ({ info }) => {
  const [slideIndex, setSlideIndex] = useState(0);

  const nextSlide = () => {
    setSlideIndex(slideIndex === info.length - 1 ? 0 : slideIndex + 1);
  };

  const prevSlide = () => {
    setSlideIndex(slideIndex === 0 ? info.length - 1 : slideIndex - 1);
  };





  const [trigger,setNav]=useState(false)

  const change=()=>{
  
      if(window.scrollY>=600){
          setNav(true)
      }
      else{
          setNav(false)
      }
  
  }

  window.addEventListener('scroll',change)


  return (
    <>
      <Place trigger={trigger}></Place>
      <Container id="gallery">
        <BUTTON_L onClick={prevSlide}>
          <FontAwesomeIcon className="i" icon={l} />
        </BUTTON_L>

        <ImageContainer>
          <List className="current--img">
            {info.map((i, index) => (
              <In
                key={index}
                className={index === slideIndex ? "slide active" : "slide"}
              >
                {index === slideIndex && <Img src={i.img}></Img>}
              </In>
            ))}
          </List>
        </ImageContainer>

        <BUTTON_R onClick={nextSlide}>
          <FontAwesomeIcon className="i" icon={r} />
        </BUTTON_R>
      </Container>
      <Place1 trigger={trigger}></Place1>
    </>
  );
};

export default Gallery;
