import React, {useState,useEffect} from 'react'
import './covid.css'
import {Banner,Close} from './covidelements'

const Covid=()=>{
    const [open, setCount] = useState(false);
// localStorage.removeItem('count')
    useEffect(() => {   

        if(localStorage.getItem('count')!=null){
            if(localStorage.getItem('count')>0){
                setCount(false)
            }
            else{
                setCount(true)
            }
        }
        else{
            setCount(true)
        }

    });

const shut=()=>{
    setCount(false)
    localStorage.setItem('count',1)

}

const load = ()=>{
    window.open("https://www.cdc.gov/coronavirus/2019-ncov/symptoms-testing/symptoms.html")
    localStorage.setItem('count',1)
    shut()
}

    return (<Banner open={open}>
        <div className="div">
        <Close onClick={shut}/>
        </div>
   <div className="text">
   <p className="p">Stay Safe.   Wear A Mask.    <span className="guide" onClick={load}>Learn more.</span></p>
   </div>
    
        </Banner>)

}

export default Covid;