import React ,{useState}from 'react';
import {Container,Content,Title} from './headingelements';
const Heading =({heading})=>{
    const [trigger,setNav]=useState(false)
    
    const change=()=>{
    
        if(window.scrollY>=600){
            setNav(true)
        }
        else{
            setNav(false)
        }
    }
    
    
    window.addEventListener('scroll',change)
return (
    <Container trigger={trigger}>
<Content><Title trigger={trigger}>
    {heading}
    </Title></Content>
</Container>
)
}

export default Heading;