import React,{useState} from 'react';
import {Container,Card,Front,Back,Text} from './cardelements';
import './card.css'
import {Place,Place1} from '../Motto/mottoelements'
const Cards =({partners})=>{
   const [trigger,setNav]=useState(false)
    
   const change=()=>{
   
       if(window.scrollY>=600){
           setNav(true)
       }
       else{
           setNav(false)
       }
   }
   
   
   window.addEventListener('scroll',change)
return (<>
   <Place trigger={trigger}/>
   <Container trigger={trigger}>{
      partners.map((i,index)=>(
         <Card key={index}>
         <Front>
               <Text className="h1">{i.name}</Text>
         </Front>
         <Back>

         </Back>
      </Card>
      ))
      
      }


   </Container>
   <Place1 trigger={trigger}/>
   </>
)
}

export default Cards;