import React,{useState} from 'react'
import {About1,H1} from './aboutelements';
const About =()=>{
    const [trigger,setNav]=useState(false)

    const change=()=>{
    
        if(window.scrollY>=600){
            setNav(true)
        }
        else{
            setNav(false)
        }
    
    }
    
    window.addEventListener('scroll',change)
return(
    <About1  trigger={trigger}>
        <H1 trigger={trigger}>   Rainbow Trust Foundation (RTF) is a Non-Governmental Organisation which seeks to aid underprivileged communities and schools with the necessary academic materials and tools to enable them to give their best to the community and country at large. The organisation strongly believes that the mind contains a lot of capabilities. The ability of the mind to explore and store useful information which is mostly underused due to lack of resources is common in most parts of our country, Ghana.</H1>
    </About1>
)

}


export default About;