import React,{useState} from "react";
import './motto.css'
import { Motto1,Content,BigText,Top,Hr,Q1,Q2,RTF,Top1,Place,Place1 } from "./mottoelements";



const Motto = () => {

    const [trigger,setNav]=useState(false)

const change=()=>{

    if(window.scrollY>=600){
        setNav(true)
    }
    else{
        setNav(false)
    }

}

window.addEventListener('scroll',change)
  return (
      <>
    <Place trigger={trigger}>

    </Place>
    <Motto1 id="about" trigger={trigger}>
      <Content>  

                <Top>
                <Hr trigger={trigger}></Hr>
                <Q1 trigger={trigger}></Q1>
                <Q2 trigger={trigger}></Q2>
                <Hr trigger={trigger}></Hr>

                </Top>

         

                <BigText className="bigtext" trigger={trigger}>
              A world where everyone has access to books that will enrich,
              improve and change their lives.
           
                  </BigText>
   


               
              

                <Top1>

           
                <RTF className="rtf" trigger={trigger}>rainbow trust</RTF>
           



                </Top1>

                


   

              
      </Content>
    </Motto1>
    <Place1 trigger={trigger}>

</Place1>
    </>
  );
};

export default Motto;
